/**
* Buttons
*/

.wp-block-button {
  @include divi {
    text-align: center;
  }
}

button,
input[type="button"],
.wp-block-button__link:not(.has-text-color),
.wp-block-button__link:not(.has-text-color):active,
.wp-block-button__link:not(.has-text-color):focus {
  position: relative;
  cursor: pointer;

  border-radius: 0;
  color: $accent_color;
  border: 2px solid;
  background-color: transparent;
  transition: all 0.2s ease-in-out;

  &:hover {
    color: $base_color;
  }

