/* slide action */
/* maybe include later with js for <ipad */

@include min-ipad{
  .slide-down,
  .slide-left,
  .slide-right,
  .slide-up{
    will-change: transform;
    transition: all 1s ease-in-out;
    transition-delay: 0.5s;
  }

  .active{
    .slide-down,
    .slide-left,
    .slide-right,
    .slide-up{
      transform:translate(0);
      opacity:1;
      &.s-absolute{
        @include divi{
          transform:translateX(-50%);
        }
      }
    }
  }

  .slide-down{
    opacity:0;
    transform: translate(0,-1000%);
    // &.slid{
    //  transform:translate(0);
    //  opacity:1;
    // }
  }
  .slide-left{
    opacity:0;
    transform: translate(1000%,0);
    // &.slid{
    //  transform:translate(0);
    //  opacity:1;
    // }
  }
  .slide-right{
    opacity:0;
    transform: translate(-1000%,0);
    // &.slid{
    //  transform:translate(0);
    //  opacity:1;
    // }
  }
  .slide-up{
    opacity:0;
    transform: translate(0,1000%);
